@import 'base.scss';
@import 'page.scss';

$light-grey: #ddd;

$mid-grey: #ccc;

$margin: 4pt;
// 2.5in
$width: 180pt;
// 3.5in
$height: 252pt;

$serif: 'Bitter', serif;
$sansSerif: 'Open Sans', sans-serif;

.card {
  //width: 180pt - ($margin * 2);
  // hack to fix margin spacing issue
  width: 230px;
  height: 252pt - ($margin * 2);
  padding: $margin;
  background: #000;
  box-sizing: border-box;
  position: relative;
  display: flex;
  flex-direction: column;
  font-size: 6pt;
  line-height: 1.4;
  font-family: $serif;
}

.page-card-inner {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

