.card-content-container {
  border-radius: 2pt;
  padding: 1pt;
  border-color: inherit;
  display: flex;
  flex-direction: column;
  flex: 1;
  font-size: inherit;
  position: relative;
  z-index: 1;
  background: #fff;
}

.card .card-content-container .content-padding {
  padding-left: 4pt;
  padding-right: 4pt;
}

.card-bg {
  text-align: center;
  position: absolute;
  bottom: 0.18in;
  left: 0;
  right: 0;
  z-index: 0;

  .bg {
    width: 80%;
    text-align: center;
    opacity: 0.1;
  }
}

.text-medium {
  font-style: normal;
  font-weight: 500;
}

.text-center {
  text-align: center;
}

.keyword {
  font-style: normal;
  //text-decoration: underline;
  font-weight: 500;

}

.heading {
  padding: 4pt;

  font-family: $sansSerif;

  .icon-wrap {
    width: 0.375in;
    height: 0.375in;
  }

  .icon {
    width: 0.375in;
    height: 0.375in;
    float: left;
    margin-right: 4pt;
  }

  .title {
    min-width: 0;
    min-height: auto;

    .primary-title {
      display: block;
      font-size: 10pt;
      font-weight: 600;
    }

    .title-note {
      font-size: 11pt;
      font-weight: normal;
    }

    .sub-title {
      display: block;
      font-size: 7pt;
    }
  }
}

.table-stats {
  font-size: 6pt;
  margin-bottom: 4pt;

  font-family: $sansSerif;

  th,
  td {
    text-align: center;
  }

  th {
    padding: 0.25em 0;
    background: $light-grey;
  }

  td {
    font-weight: 600;
  }
}

.table-weapons {
  font-size: 6pt;
  // cell align to the right hack
  max-width: 220pt;

  th,
  td {
    text-align: center;
    vertical-align: baseline;
    width: 100%;
  }

  th {
    padding: 0.3em 0.5em;
    background: $light-grey;
    font-size: 5pt;
    font-family: $sansSerif;

    &:first-child {
      padding-left: 4pt;
      text-align: left;
    }

    &:last-child {
      padding-right: 4pt;
    }
  }


  .weapon-row {

    .col-target,
    .col-rof,
    .col-range,
    .col-effect {
      white-space: nowrap
    }

    &:first-child td {
      border-top: 0;
    }

    td {
      border-top: 1px solid hsl(0, 0%, 60%);
      font-family: $sansSerif;
      padding: 2pt 0 3pt;


      &:last-child {
        padding-right: 4pt;
      }

      &.col-name,
      &.col-target,
      &.col-rof,
      &.col-range,
      &.col-effect {
        font-weight: 500;
      }

      &.col-target {
        font-size: 90%;
        font-weight: bold;
      }

      &.col-effect {
        text-transform: uppercase;
        font-size: 90%;
        font-weight: bold;
      }
    }
  }


  .weapon-description-row td,
  .weapon-traits-row td {
    padding-left: 4pt;
    padding-right: 4pt;
  }

  .weapon-description-row td {
    font-size: 90%;
    padding-top: 0.5em;

    &:first-child {
      padding-top: 0;
    }
  }

  td {
    &:first-child {
      padding-left: 4pt;
      text-align: left;
    }
  }
}

.weapon-traits,
.sub-traits {

  .weapon-trait,
  .sub-trait {

    &:first-child {
      //margin-top: .125em;
      margin-top: 0;
    }

    padding: 0 0 0 4pt;

    &::before {
      display: inline-block;
      margin: 0 0 0 -4pt;
      content: 'Â»'
    }
  }
}

.section-heading {
  font-size: 5pt;
  margin: 0;
  background: $light-grey;
  padding: 1pt 4pt;
  vertical-align: middle;
  font-family: $sansSerif;
  font-weight: bold;
}

.key-value {
  margin: 0.5em 0;
  font-family: $serif;
  font-weight: normal;
  font-size: 90%;

  .key {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 90%;
    font-family: $sansSerif;

  }

  .note {
    font-weight: 500;
    font-size: 90%;
    font-family: $sansSerif;
  }
}


.sub-footer {
  font-family: $sansSerif;
  font-size: 3.5pt;
  color: #808080;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1pt 0;
  line-height: 1;
}


